body {
  font-family: "Work Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 1.6;
  background-color: #FBFBFE;
}

p {
  color: #2A3B47;
  font-size: 2rem;
  font-family: "Work Sans", sans-serif;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  margin: 2rem 0;
  text-align: center;
}

h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 3.8rem;
  font-weight: 700;
}

h2 {
  font-family: "Hind Madurai", sans-serif;
  font-size: 3.4rem;
  font-weight: 700;
}

h3 {
  font-family: "Hind Madurai", sans-serif;
  font-size: 3rem;
  font-weight: 600;
}

h4 {
  font-family: "Hind Madurai", sans-serif;
  font-size: 2.6rem;
  font-weight: 600;
}

.seccion {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton-secundario {
  background-color: #1083D6;
  color: #FBFBFE;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  text-transform: uppercase;
  margin-top: 2rem;
  display: inline-block;
  border: none;
  cursor: pointer;
}
.boton-secundario:hover {
  background-color: #0d6cb0;
}

.boton-secundario-block {
  background-color: #1083D6;
  color: #FBFBFE;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  text-transform: uppercase;
  margin-top: 2rem;
  display: block;
  border: none;
  cursor: pointer;
}
.boton-secundario-block:hover {
  background-color: #0d6cb0;
}

.boton-rojo {
  background-color: #b91c1c;
  color: #FBFBFE;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  text-transform: uppercase;
  margin-top: 2rem;
  display: inline-block;
  border: none;
  cursor: pointer;
}
.boton-rojo:hover {
  background-color: #961717;
}

.boton-rojo-delete {
  background-color: #b91c1c;
  color: #FBFBFE;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  text-transform: uppercase;
  margin-top: 2rem;
  display: inline-block;
  border: none;
  cursor: pointer;
}
.boton-rojo-delete:hover {
  background-color: #961717;
}

.boton-verde-block {
  background-color: #15803d;
  color: #FBFBFE;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  text-transform: uppercase;
  margin-top: 2rem;
  display: block;
  border: none;
  cursor: pointer;
}
.boton-verde-block:hover {
  background-color: #0f5d2c;
}

.boton-amarillo-update {
  background-color: #dcb902;
  color: #FBFBFE;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  text-transform: uppercase;
  margin-top: 2rem;
  display: inline-block;
  border: none;
  cursor: pointer;
}
.boton-amarillo-update:hover {
  background-color: #b39702;
}

.dark-mode {
  background-color: #192229;
}
.dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #EFF3F5;
}
.dark-mode p, .dark-mode label {
  color: #C8CDD0;
}
.dark-mode .contenido-anuncio, .dark-mode .resumen-propiedad {
  background-color: #212E36;
  border: 1px solid #2A3B47;
}
.dark-mode .contenido-anuncio .precio, .dark-mode .resumen-propiedad .precio {
  color: #FBFBFE;
}
.dark-mode .contenido-anuncio .icono, .dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #2A3B47;
  padding: 1rem 0 2rem 0;
}
@media (min-width: 768px) {
  .header {
    padding: 1rem 0;
  }
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h2 {
  text-align: left;
  color: #FBFBFE;
  line-height: 1.8;
  max-width: 60rem;
  text-wrap: pretty;
  font-size: 2.3rem;
  margin-top: 4rem;
}
@media (min-width: 768px) {
  .contenido-header h2 {
    margin: 2rem 0;
    font-size: 3.4rem;
  }
}

.footer {
  background-color: #2A3B47;
  padding-top: 2rem;
  margin-bottom: 0;
}
.footer .contenido-footer {
  padding: 4rem;
}
.footer .copyright {
  color: #FBFBFE;
  text-align: center;
  padding: 1rem 0;
  margin: 0;
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  .footer .copyright {
    font-size: 1.6rem;
  }
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  margin-top: 0;
}

.barra {
  margin-top: 2rem;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 0;
  }
}
.barra .bloque-1 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  color: #FBFBFE;
}
.logo h1 {
  font-weight: 300;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  margin: 0;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .logo h1 {
    font-size: 3.8rem;
  }
}
.logo span {
  font-weight: 900;
}

.navegacion {
  margin-top: -25rem;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, margin-top 0.5s ease-in-out;
  flex-direction: column;
  justify-content: flex-end;
  background-color: rgba(240, 240, 240, 0.5);
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .navegacion {
    margin-top: 0;
    display: flex;
    background-color: transparent;
    flex-direction: row;
    column-gap: 3rem;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FBFBFE;
  text-decoration: none;
  font-size: 1.6rem;
  display: block;
  text-align: center;
  padding: 1rem;
}
@media (min-width: 768px) {
  .navegacion a {
    padding: 0;
    font-size: 1.8rem;
  }
}
.navegacion img {
  width: 3.5rem;
  cursor: pointer;
  margin: 0 auto;
  padding: 1rem 0;
  background-color: transparent;
}
@media (min-width: 768px) {
  .navegacion img {
    margin: 0;
    padding: 0;
  }
}

.mobile-menu {
  display: flex;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 3.5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  margin-top: 0;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

.contenedor-anuncios {
  display: grid;
  row-gap: 2rem;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #FBFBFE;
  border: 1px solid #d7dadb;
  border-radius: 0.8rem;
  overflow: hidden;
  -webkit-box-shadow: 0px 0px 6px -1px rgba(196, 196, 196, 0.199);
  -moz-box-shadow: 0px 0px 6px -1px rgba(0, 0, 0, 0.35);
  box-shadow: 0px 0px 6px -1px rgba(117, 116, 116, 0.35);
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3,
.anuncio p {
  margin: 0;
  text-align: center;
}
.anuncio .precio {
  color: green;
  font-weight: 700;
  font-size: 2.6rem;
  text-align: left;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
  align-items: center;
}
.iconos-caracteristicas li img {
  flex: 0 0 5rem;
  margin-right: 1.5rem;
}
.iconos-caracteristicas li p {
  font-weight: 700;
}

.resumen-propiedad {
  border-radius: 0.5rem;
}
.resumen-propiedad p {
  padding: 2rem;
}

.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg);
}

.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp);
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 4rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  margin-top: 0;
  font-weight: 700;
  font-size: 4rem;
  color: #FBFBFE;
}
.imagen-contacto p {
  color: #FBFBFE;
}

@media (min-width: 768px) {
  .imagen-contacto {
    padding: 4rem 0;
  }
}
@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #b91c1c;
}

.texto-entrada a {
  color: #2A3B47;
  display: block;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #697477;
  margin-top: 1rem;
}
.texto-entrada span {
  color: #b91c1c;
}

.testimonial {
  background-color: #697477;
  font-size: 2rem;
  padding: 2rem;
  color: #FBFBFE;
  border-radius: 1rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/quote.svg);
  background-repeat: no-repeat;
  background-size: contain;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
  top: 1rem;
}
.testimonial p {
  text-align: right;
  color: #FBFBFE;
}
@media (min-width: 768px) {
  .testimonial {
    font-size: 2.4rem;
  }
}

.formulario p {
  font-size: 1.4rem;
  color: #697477;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #697477;
}
.formulario label {
  font-weight: bold;
  display: block;
  text-transform: uppercase;
}
.formulario input:not([type=submit]), .formulario textarea, .formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FBFBFE;
  border: 1px solid #A0A7AC;
  border-radius: 0.5rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

.sobre-nosotros {
  display: grid;
}
@media (min-width: 768px) {
  .sobre-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.nosotros-texto h3 {
  text-align: left;
